import { useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'

import { Box, Card, Container, Divider } from '@mui/material'

import type { ClubMemberForAuth } from 'src/models/ClubMember'

import ChangeClubDate from 'src/content/manage/Settings/ChangeClubDate'
import PageTitleWrapper from 'src/components/PageTitleWrapper'

import PageHeader from 'src/content/manage/PageHeader'
import SearchableSetting from 'src/content/manage/Settings/SearchableSetting'

import useUpdateAuthState from 'src/hooks/useUpdateAuthUser'
import { usePatchClub } from 'src/hooks/api/club'

import InstagramId from './InstagramId'

const SettingsPage: React.FC = () => {
  const authUser = useAuthUser()

  const params = useParams()
  const clubId = Number(params.id)
  const club = authUser()?.clubs.filter((c: { id: number }) => c.id === clubId)[0]
  const updateAuthState = useUpdateAuthState()
  const patchClubMutation = usePatchClub()
  const handleSwitchChange: React.ChangeEventHandler<HTMLInputElement> = async e => {
    try {
      await patchClubMutation.mutateAsync({ clubId, data: { searchable: e.target.checked } })
      const unsafeUser = authUser()
      if (!unsafeUser) {
        throw Error
      }
      const clubs = unsafeUser.clubs.map((club: ClubMemberForAuth) => {
        return {
          ...club,
          searchable: club.id === clubId ? !club.searchable : club.searchable
        }
      })
      updateAuthState(unsafeUser, clubs)
    } catch {}
  }

  return (
    <>
      <Helmet>
        <title>동아리 설정</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader
          clubName={club.name}
          buttonText={'회원등급 관리'}
          link={`/manage/${clubId}/settings/member-level`}
        />
      </PageTitleWrapper>
      <Container maxWidth={'lg'}>
        <Card sx={{ minHeight: '400px' }}>
          {/* <ClubSummary /> */}
          <Box mx={2} my={2}>
            <InstagramId clubId={clubId} />
          </Box>
          <Divider />
          <Box mx={2} my={2}>
            <SearchableSetting
              handleSwitchChange={handleSwitchChange}
              defaultChecked={club.searchable}
              clubName={club.name}
            />
          </Box>
          <Divider />
          <Box mx={2} my={2}>
            <ChangeClubDate clubId={clubId} />
          </Box>
          <Divider />
        </Card>
      </Container>
    </>
  )
}

export default SettingsPage
