import { useState } from 'react'
import { useAuthUser } from 'react-auth-kit'

import { Box, Button, Typography, useMediaQuery, useTheme } from '@mui/material'

import {
  FIRST_SEMESTER,
  FIRST_SEMESTER_MONTH,
  FIRST_SEMESTER_NAME,
  SECOND_SEMESTER,
  SECOND_SEMESTER_MONTH,
  SECOND_SEMESTER_NAME
} from 'src/data/semester'

import type { ClubMemberForAuth } from 'src/models/ClubMember'
import type { DateProps, Semester } from 'src/models/date'

import ConfirmDialog from 'src/components/ConfirmDialog'

import { usePatchClub } from 'src/hooks/api/club'
import useUpdateAuthState from 'src/hooks/useUpdateAuthUser'

interface DateFormProps {
  clubId: number
}

const getSemesterName = (semester: Semester): string => {
  return semester === FIRST_SEMESTER ? FIRST_SEMESTER_NAME : SECOND_SEMESTER_NAME
}

const ChangeClubDate: React.FC<DateFormProps> = ({ clubId }) => {
  const [confirmMode, setConfirmMode] = useState<boolean>(false)

  const { breakpoints } = useTheme()
  const isUpperThanSm = useMediaQuery(breakpoints.up('sm'))

  const patchClubMutation = usePatchClub()

  const authUser = useAuthUser()
  const updateAuthState = useUpdateAuthState()
  const club = authUser()?.clubs.filter((c: ClubMemberForAuth) => c.id === clubId)[0] as ClubMemberForAuth
  const currentDate = new Date()

  const nextClubDate: DateProps = {
    year: club.semester === SECOND_SEMESTER ? club.year + 1 : club.year,
    semester: club.semester === SECOND_SEMESTER ? FIRST_SEMESTER : SECOND_SEMESTER
  }

  // [기획] 직전학기가 FALL이라면 2월이 지나야 이월 가능, SPRING이라면 8월이 지나야 이월 가능
  const firstDateOnFirstSemester = new Date(`${nextClubDate.year}-${FIRST_SEMESTER_MONTH}`)
  const firstDateOnSecondSemester = new Date(`${nextClubDate.year}-${SECOND_SEMESTER_MONTH}`)

  const isForwardable =
    club.semester === FIRST_SEMESTER ? currentDate > firstDateOnSecondSemester : currentDate > firstDateOnFirstSemester

  const handleOpenModal: React.MouseEventHandler<HTMLButtonElement> = () => {
    setConfirmMode(true)
  }

  const handleCloseModal: React.MouseEventHandler<HTMLButtonElement> = () => {
    setConfirmMode(false)
  }

  const handleSubmit = async () => {
    if (nextClubDate.semester && nextClubDate.year) {
      try {
        await patchClubMutation.mutateAsync({
          clubId,
          data: { semesterInfo: nextClubDate }
        })
        const unsafeUser = authUser()
        if (!unsafeUser) {
          throw Error
        }
        const clubs = unsafeUser.clubs.map((club: ClubMemberForAuth) => {
          return {
            ...club,
            semester: club.id === clubId ? nextClubDate.semester : club.semester,
            year: club.id === clubId ? nextClubDate.year : club.year
          }
        })
        updateAuthState(unsafeUser, clubs)
      } catch (err) {}
    }
    setConfirmMode(false)
  }

  return (
    <Box>
      <Typography variant={'h4'}>학기 이월하기</Typography>
      {club.semester && club.year && (
        <Box mt={2}>
          <Box>
            <Typography variant={'h5'} display={'inline'}>
              현재 학기 : <ColoredTypography text={`${club.year}`} />
              년도&nbsp;
              <ColoredTypography text={getSemesterName(club.semester)} />
            </Typography>
          </Box>

          {isForwardable ? (
            <>
              <Typography variant={'h5'} display={'inline'}>
                <ColoredTypography text={`${nextClubDate.year}`} />
                년도&nbsp;
                <ColoredTypography text={getSemesterName(nextClubDate.semester)} />로 변경합니다.
              </Typography>
              <Box my={2}>
                <Button color="error" variant="outlined" fullWidth={!isUpperThanSm} onClick={handleOpenModal}>
                  학기 이월
                </Button>
              </Box>
              <Typography variant="caption">
                학기 이월이란, 앞으로의 모든 활동 및 정보 입력은 다음 학기로 설정이 된다는 뜻입니다. 번복할 수 없으니
                신중하게 결정해 주세요
              </Typography>
            </>
          ) : (
            <Typography variant={'body2'} display={'inline'}>
              다음 학기 이월은&nbsp;
              {nextClubDate.semester === SECOND_SEMESTER
                ? SECOND_SEMESTER_MONTH
                : `${nextClubDate.year}년도 ${FIRST_SEMESTER_MONTH}`}
              월 이후부터 가능합니다.
            </Typography>
          )}
        </Box>
      )}

      <ConfirmDialog
        open={confirmMode}
        handleClose={handleCloseModal}
        handleSubmit={handleSubmit}
        confirmMessage={<ConfirmMessage year={nextClubDate.year} semester={nextClubDate.semester} />}
      />
    </Box>
  )
}

export default ChangeClubDate

interface ColoredTypographyProps {
  text: string
}

const ColoredTypography: React.FC<ColoredTypographyProps> = ({ text }) => {
  return (
    <Typography color="primary" variant="inherit" display={'inline'}>
      {text}
    </Typography>
  )
}

const ConfirmMessage: React.FC<DateProps> = ({ year, semester }) => (
  <Typography variant="h4" color={'GrayText'}>
    {`지금부터 생성되는 회의, 지원, 정보 수정 등의 모든 작업은 `}
    <ColoredTypography text={`${year}년 ${getSemesterName(semester)}`} />
    {` 를 기준으로 진행되며, 번복이 불가합니다. 정말로 설정하시겠습니까?`}
  </Typography>
)
