import axios, { AxiosError, type AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import type { UseMutationResult } from 'react-query'
import { useMutation, useQuery, useQueryClient } from 'react-query'

import { COMMON_MESSAGE } from 'src/data/messages'

import type { ClubId } from 'src/models/Club'

interface GetInstagramParameters {
  clubId: ClubId
  token: string
}

export interface ClubLinksResponseData {
  instagramId: string
}

const getInstagramAPICall =
  ({ clubId, token }: GetInstagramParameters) =>
  async () => {
    const response = await axios.get<ClubLinksResponseData>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/instagram`,
      {
        headers: { Authorization: token }
      }
    )
    return response.data
  }

export const useGetInstagram = (clubId: ClubId) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  return useQuery<ClubLinksResponseData>(['instagramId'], getInstagramAPICall({ clubId, token }), {
    onError: error => {
      if (error instanceof AxiosError) {
        enqueueSnackbar(COMMON_MESSAGE.SERVER_ERROR, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    },
    retry: 0
  })
}

export interface PatchInstagramReqeustData {
  instagramId: string
}

export type PatchInstagramParams = {
  clubId: ClubId
  clubLinks: PatchInstagramReqeustData
}

const putInstagramAPICall = async ({ clubId, clubLinks }: PatchInstagramParams, token: string) => {

  await axios.patch<null, AxiosResponse<null>, PatchInstagramReqeustData>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/instagram`,
    clubLinks,
    {
      headers: { Authorization: token }
    }
  )
}

export const useEditInstagram = (): UseMutationResult<void, unknown, PatchInstagramParams, void> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  const mutationFn = (values: PatchInstagramParams) => putInstagramAPICall(values, token)

  return useMutation('putInstagram', mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.UPDATE_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('instagramId')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        enqueueSnackbar(COMMON_MESSAGE.UPDATE_FAIL, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}
