import axios, { AxiosError, type AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import { useMutation } from 'react-query'

import { COMMON_MESSAGE, SETTING_MESSAGE } from 'src/data/messages'

import type { ClubId } from 'src/models/Club'
import type { DateProps } from 'src/models/date'

export interface PatchClubBody {
  semesterInfo?: DateProps
  searchable?: boolean
}
interface PatchMutationParams {
  clubId: ClubId
  data: PatchClubBody
}
interface PatchParams extends PatchMutationParams {
  token: string
}
const patchClub = async ({ clubId, token, data }: PatchParams) => {
  await axios.patch<null, AxiosResponse<null>, PatchClubBody>(`${process.env.REACT_APP_APIHOST}/club/${clubId}`, data, {
    headers: { Authorization: token }
  })
}
export const usePatchClub = () => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  const mutateFn = ({ clubId, data }: PatchMutationParams) => patchClub({ clubId, token, data })

  return useMutation(mutateFn, {
    onSuccess: () => {
      enqueueSnackbar(SETTING_MESSAGE.SUCCESS, { variant: 'success' })
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || SETTING_MESSAGE.FAIL
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}
