import { Box, Switch, Typography } from '@mui/material'

const label = { inputProps: { 'aria-label': '동아리 공개하기' } }

interface handleSwitchChange {
  handleSwitchChange: React.ChangeEventHandler<HTMLInputElement>
  defaultChecked: boolean
  clubName: string
}
const SearchableSetting: React.FC<handleSwitchChange> = ({ handleSwitchChange, defaultChecked, clubName }) => {
  return (
    <Box display="flex" justifyContent="space-between">
      <Box>
        <Typography variant={'h4'}>동아리 공개하기</Typography>
        <Box mt={2}>
          <Typography>
            공개 시 동아리 검색에 {clubName}이(가) 노출됩니다. 동아리를 홍보하고 바로 회원을 모집할 수 있습니다.
          </Typography>
        </Box>
      </Box>
      <Box display="flex" alignItems="center">
        <Switch onChange={handleSwitchChange} {...label} defaultChecked={defaultChecked} checked={defaultChecked} />
      </Box>
    </Box>
  )
}

export default SearchableSetting
