import { useEffect, useState } from 'react'

import { Box, Button, Grid, TextField, Typography } from '@mui/material'

import type { ClubId } from 'src/models/Club'

import type { ClubLinksResponseData } from 'src/hooks/api/settings'
import { useEditInstagram, useGetInstagram } from 'src/hooks/api/settings'

const InstagramId: React.FC<{ clubId: ClubId }> = ({ clubId }) => {
  const [clubLinks, setClubLinks] = useState<ClubLinksResponseData>({ instagramId: '' })
  const { data, isSuccess } = useGetInstagram(clubId)
  const editClubLinks = useEditInstagram()

  useEffect(() => {
    if (isSuccess) setClubLinks({ instagramId: data.instagramId ?? '' })
  }, [isSuccess])

  const handleChange = (name: string, value: string) => {
    setClubLinks(prevState => ({
      ...prevState,
      [name]: value
    }))
  }
  const handleClickEditButton: React.MouseEventHandler<HTMLButtonElement> = () => {
    if (isSuccess) {
      editClubLinks.mutate({ clubId, clubLinks })
    }
  }

  return (
    <Box>
      <Box width={'100%'}>
        <Typography variant={'h4'}>동아리 링크 설정</Typography>
        <Grid container alignItems="center" mt={2} spacing={2}>
          <Grid item xs={12} sm={6} sx={{ display: { xs: 'flex' }, justifyContent: { xs: 'center', sm: 'flex-start' } }}>
            <TextField
              label="인스타그램 아이디"
              name="instagramId"
              id="limit"
              value={clubLinks.instagramId}
              onChange={e => handleChange(e.target.name, e.target.value)}
            />
          </Grid>
          <Grid item xs={12} sm={6} display={'flex'} sx={{justifyContent: { xs: 'center', sm: 'flex-end' } }}>
            <Button variant="contained" color="success" onClick={handleClickEditButton}>
              등록 / 수정
            </Button>
          </Grid>
        </Grid>
      </Box>
    </Box>
  )
}

export default InstagramId
